.text-center {
  text-align: center;
}

.mt-5 {
  margin-top: 5rem;
}

.bg-primario {
  background-color: hsl(171deg, 66%, 44%);
}

.buttons {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.button {
  color: #FFFFFF;
  border-radius: 3rem;
  display: inline-block;
  text-align: center;
  margin-bottom: 3rem;
  padding: 1rem 0;
  border: 1px solid transparent;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  outline: none;
  cursor: pointer;
}

.ios {
  background-color: hsl(171deg, 66%, 44%);
}

.mac {
  background-color: hsl(233deg, 100%, 69%);
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Bai Jamjuree", sans-serif;
  line-height: 1.5;
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h3, h2 {
  color: hsl(210deg, 10%, 33%);
  font-size: 3.2rem;
  text-align: center;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2rem;
}

p {
  color: hsl(201deg, 11%, 66%);
  margin: 1rem 0 5rem 0;
  text-align: center;
}

.header {
  background-image: url(../img/bg-header-mobile.png);
  background-repeat: no-repeat;
  background-position: top center;
}
@media (min-width: 375px) {
  .header {
    background-image: url(../img/bg-header-desktop.png);
  }
}
.header .header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 1.5rem;
}
.header .header-content .logo {
  padding: 7rem 0;
}
.snippets .snippets-content {
  padding: 3rem 1.5rem;
}
.snippets .snippets-content .imagen {
  margin-bottom: 5rem;
}

.access .access-content {
  padding: 3rem 1.5rem;
}
.access .access-content .imagen {
  margin-bottom: 3rem;
}

.features .features-grid {
  padding: 3rem 1.5rem;
}
.features .features-grid .feature {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.features .features-grid .feature .logo {
  padding: 2rem 0;
}

.partners .partners-grid {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 3rem 1.5rem;
}
.partners .partners-grid .partner {
  margin: 4rem 0;
}

.downloads .downloads-content {
  padding: 3rem 1.5rem;
}
.footer {
  background-color: hsl(220deg, 18%, 97%);
}
.footer .footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 1.5rem;
}
.footer .footer-content .logo {
  width: 5rem;
}
.footer .footer-content .links {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer .footer-content .links li {
  color: hsl(210deg, 10%, 33%);
  font-size: 1.8rem;
  margin: 3rem 0;
}
.footer .footer-content .socials {
  display: flex;
  width: 50%;
  justify-content: space-around;
}
.footer .footer-content .socials .social i {
  font-size: 2.5rem;
  color: hsl(210deg, 10%, 33%);
}
.footer .attribution {
  font-size: 1rem;
  text-align: center;
  margin: 0;
}
.footer .attribution a {
  color: hsl(228deg, 45%, 44%);
}